import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AllPensionContributionComponent } from './pension-contribution/all-pension-contribution/all-pension-contribution.component';
import { AllPensionEnrolmentComponent } from './pension-enrolment/all-pension-enrolment/all-pension-enrolment.component';
import { RoutePrivilegeGuard } from 'src/app/erp-procurement/data/services/_AccessControlAuthGuard.service';

const routes: Routes = [
  {
    path: "all-pension-contributions",
    component: AllPensionContributionComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },
  {
    path: "all-pension-enrolment",
    component: AllPensionEnrolmentComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PensionRoutingModule { }
